import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Tabs
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const TabPane = Tabs.TabPane

@connect(
  state => ({...state.outWarehouseListSell, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [
      {
        title: '总量',
        children: [
          {
            title: '商品编码',
            width: 150,
            dataIndex: 'productCode',
            key: 'productCode'
          }, {
            title: '商品名称',
            width: 150,
            dataIndex: 'productName',
            key: 'productName'
          }, {
            title: '单位',
            width: 100,
            dataIndex: 'unit',
            key: 'unit'
          }, {
            title: '单价（元）',
            width: 100,
            dataIndex: 'price',
            key: 'price'
          }, {
            title: '数量',
            width: 100,
            dataIndex: 'planQty',
            key: 'planQty'
          }, {
            title: '发货数',
            width: 150,
            dataIndex: 'shippedQty',
            key: 'shippedQty'
          }, {
            title: '收货数',
            width: 100,
            dataIndex: 'receiveQty',
            key: 'receiveQty'
          }, {
            title: '总金额（元）',
            width: 100,
            dataIndex: 'amount',
            key: 'amount'
          }, {
            title: '发货全量',
            width: 100,
            dataIndex: 'allSendFlag',
            key: 'allSendFlag'
          }, {
            title: '备注',
            width: 100,
            dataIndex: 'remark',
            key: 'remark'
          }
        ]
      }
    ]
    this.columnsT03 = [
      {
        title: '总量',
        children: [
          {
            title: '商品编码',
            width: 150,
            dataIndex: 'productCode',
            key: 'productCode'
          }, {
            title: '商品名称',
            width: 150,
            dataIndex: 'productName',
            key: 'productName'
          }, {
            title: '单位',
            width: 100,
            dataIndex: 'unit',
            key: 'unit'
          }, {
            title: '单价（元）',
            width: 100,
            dataIndex: 'price',
            key: 'price'
          }, {
            title: '申偿数',
            width: 100,
            dataIndex: 'passQty',
            key: 'passQty'
          }, {
            title: '发货数',
            width: 150,
            dataIndex: 'shippedQty',
            key: 'shippedQty'
          }, {
            title: '拒绝数',
            width: 100,
            dataIndex: 'rejectQty',
            key: 'rejectQty'
          }, {
            title: '总金额（元）',
            width: 100,
            dataIndex: 'amount',
            key: 'amount'
          }, {
            title: '发货全量',
            width: 100,
            dataIndex: 'allSendFlag',
            key: 'allSendFlag'
          }, {
            title: '备注',
            width: 100,
            dataIndex: 'remark',
            key: 'remark'
          }
        ]
      }
    ]
    this.columns2 = [{
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '单价（元）',
      width: 100,
      dataIndex: 'price',
      key: 'price'
    }, {
      title: '发货数',
      width: 100,
      dataIndex: 'shippedQty',
      key: 'shippedQty'
    }, {
      title: '总金额（元）',
      width: 100,
      dataIndex: 'amount',
      key: 'amount'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleTabDetail, match: {params}} = this.props
    handleTabDetail({searchParams: {orderCode: params.id}})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {history} = this.props
    history.length > 1 ? history.goBack() : window.close()
  }
  render() {
    const {modalData, modalData: {shippedResponse}, deliveryTableLoading, activeKey, update} = this.props
    return (
      <div className='fullOrderDel'>
        <span className='ant-modal-close-x close' onClick={this.handleCloseModal}></span>
        <Tabs activeKey={activeKey} onChange={(key) => {
          update({activeKey: key})
        }}>
          <TabPane tab={'出库单详情'} key="1">
            <div className='content'>
              <div className='header'>
                <Row type="flex" justify="start">
                  <Col span={6} className='delBox'>
                    <label>出库单号：</label>
                    <span>{shippedResponse && shippedResponse.orderCode || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>外部单号：</label>
                    <span>{shippedResponse && shippedResponse.externalOrderCode || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>出库地点：</label>
                    <span>{`${shippedResponse && shippedResponse.sendLocationCode || ''}-${shippedResponse && shippedResponse.sendLocationName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>货主：</label>
                    <span>{shippedResponse && shippedResponse.shipperName || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>业务类型：</label>
                    <span>{shippedResponse && shippedResponse.businessTypeDesc || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>订单状态：</label>
                    <span>{shippedResponse && shippedResponse.statusDesc || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>出库单打印次数：</label>
                    <span>{shippedResponse && shippedResponse.printTimes || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>配送方：</label>
                    <span>{`${shippedResponse && shippedResponse.deliveryCode || ''}-${shippedResponse && shippedResponse.deliveryName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>供应商：</label>
                    <span>{shippedResponse && shippedResponse.supplierName || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>配送门店：</label>
                    <span>{`${shippedResponse && shippedResponse.shopCode || ''}-${shippedResponse && shippedResponse.shopName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>站点：</label>
                    <span>{`${shippedResponse && shippedResponse.stationCode || ''}-${shippedResponse && shippedResponse.stationName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>配送客户：</label>
                    <span>{`${shippedResponse && shippedResponse.customerCode || ''}-${shippedResponse && shippedResponse.customerName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>收货人：</label>
                    <span>{shippedResponse && shippedResponse.receiveName || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>收货人电话：</label>
                    <span>{shippedResponse && shippedResponse.receivePhoneNumber || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>收货地址：</label>
                    <span>{shippedResponse && shippedResponse.receiveAddress || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>订单总额：</label>
                    <span>{modalData.orderAmount}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>运费：</label>
                    <span>{shippedResponse && shippedResponse.deliveryFee || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>出库时间：</label>
                    <span>{shippedResponse && shippedResponse.sendTime || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>收货地点：</label>
                    <span>{`${shippedResponse && shippedResponse.receiveLocationCode || ''}-${shippedResponse && shippedResponse.receiveLocationName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>转配地点：</label>
                    <span>{`${shippedResponse && shippedResponse.transferLocationCode || ''}-${shippedResponse && shippedResponse.transferLocationName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>签收日期：</label>
                    <span>{modalData.signDate || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>备注：</label>
                    <span>{shippedResponse && shippedResponse.remark || ''}</span>
                  </Col>
                </Row>
                <Row type="flex" justify="center" gutter={16} style={{marginTop: '28px'}}>
                  <Col className='delBox'>
                    <label>商品数量（已出/总量）：</label>
                    <span>{modalData.shippedProductCount} /{modalData.productCount}</span>
                  </Col>
                  <Col className='delBox'>
                    <label>商品种类（已出/总量）：</label>
                    <span>{modalData.shippedProductCategoryCount} /{modalData.productCategoryCount}</span>
                  </Col>
                </Row>
              </div>
              <div className='foot'>
                <div className='table-group'>
                  <Table
                    rowKey={record => record.id}
                    loading={deliveryTableLoading}
                    columns={(shippedResponse && shippedResponse.shippedType == 'T03') ? this.columnsT03 : this.columns}
                    scroll={{x: 1150, y: 400}}
                    dataSource={modalData.shippedOrderItemList}
                    pagination={false}
                  />
                </div>
              </div>
            </div>
          </TabPane>
          <TabPane tab={`出库明细（${modalData.shippedBatchCount}）`} key="2">
            {
              modalData.shippedBatchList && modalData.shippedBatchList.map((o, i) => <div className='table-group' key={o.batchCode}>
                  <div className='header'>
                    <Row type="flex" justify="start">
                      <Col span={6} className='delBox'>
                        <label>出库批次：</label>
                        <span>{o.batchCode || ''}</span>
                      </Col>
                      <Col span={6} className='delBox'>
                        <label>创建时间：</label>
                        <span>{o.createTime || ''}</span>
                      </Col>
                      <Col span={6} className='delBox'>
                        <label>操作人：</label>
                        <span>{o.createBy || ''}</span>
                      </Col>
                      <Col span={6} className='delBox'>
                        <label>是否冲销：</label>
                        <span>{o.writeOffFlag ? '已冲销' : '未冲销'}</span>
                      </Col>
                    </Row>
                  </div>
                  <Table
                    rowKey={(record, index) => index}
                    loading={deliveryTableLoading}
                    columns={this.columns2}
                    scroll={{x: 700, y: 400}}
                    dataSource={o.shippedBatchDetailList}
                    pagination={false}
                  />
                </div>)
            }
          </TabPane>
        </Tabs>

      </div>
    )
  }
}



